type ProductType = {
  id: number;
  name: string;
  keywords: string[];
  values: number[];
  color: ColorType;
  imgSrc: string;
  bar_desc?: string;
};

export const getTagValues: (values: string[]) => number[] = (values) => {
  const tagValues: Record<string, number> = {};
  const pointRate = 2.5;
  values.forEach((keyword) => {
    const tag = keywordMap.get(keyword);
    if (tag) {
      tagValues[tag] = (tagValues[tag] || 0) + 1;
    }
  });
  return tags.map((tag) => (tagValues[tag] || 0) * pointRate);
};

