import React, { useState } from 'react';
import Page0 from 'components/pages/Page0';
import Page1 from 'components/pages/Page1';
import Page2 from 'components/pages/Page2';
import Page3 from 'components/pages/Page3';
import Page4 from 'components/pages/Page4';
import Page5 from 'components/pages/Page5';
import Page6 from 'components/pages/Page6';
import Page7 from 'components/pages/Page7';
import SimulatorContext, { StateType } from './context';

const pages = [
  <Page0 />,
  <Page1 />,
  <Page2 />,
  <Page3 />,
  <Page4 />,
  <Page5 />,
  <Page6 />,
  <Page7 />,
];
const minPage = 0;
const maxPage = pages.length - 1;

const Simulator: React.FC = () => {
  const [state, setState] = useState<StateType>({
    page: minPage,
    product: null,
    media: null,
    model: null,
  });
  const goToHome = () => {
    setState((currentState) => {
      return {
        ...currentState,
        page: minPage,
      };
    });
  };
  const handleGoToPage = (addValue: number) => () => {
    setState((currentState) => {
      let newPage = currentState.page + addValue;
      if (!(minPage <= newPage && newPage <= maxPage)) {
        newPage = minPage;
      }
      return {
        ...currentState,
        page: newPage,
      };
    });
  };
  const goToPrev = handleGoToPage(-1);
  const goToNext = handleGoToPage(1);
  const page = pages[state.page];
  return (
    <SimulatorContext.Provider
      value={{ state, setState, goToHome, goToPrev, goToNext }}
    >
      {page}
    </SimulatorContext.Provider>
  );
};

export default Simulator;
