import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import Container from 'components/common/Container';
import SquareButton from 'components/common/SquareButton';
import PageTitle from 'components/common/PageTitle';
import PageSubtitle from 'components/common/PageSubtitle';
import TopSection from 'components/common/TopSection';
import BottomSection from 'components/common/BottomSection';
import SimulatorContext from 'components/Simulator/context';
import { datas } from 'utils/constants';
import Dialog from 'components/common/Dialog';
import DialogDetailPage from '../DialogDetailPage';
import BasePage from '../BasePage';

const ButtonDiv = styled.div`
  margin: 0 1rem;
  text-align: center;
`;
const TextDiv = styled.div`
  font-size: 1.2rem;
  color: #717578;
  line-height: 1.4;
  margin: 1rem 0 0 1rem;
  width: 17rem;
`;

const Page: React.FC = () => {
  const {
    state: { media },
    setState,
    goToNext,
  } = useContext(SimulatorContext);
  const [open, setOpen] = useState(false);
  const setMedia = (mediaId: number | null) =>
    setState((currentState) => {
      return {
        ...currentState,
        media: mediaId,
      };
    });
  return (
    <BasePage>
      <Container>
        <TopSection>
          <PageTitle>매체 고르기</PageTitle>
          <PageSubtitle>
            광고 매체별 특성을 이해하고, 제품과 가장 알 맞는 매체를 고르세요!
          </PageSubtitle>
        </TopSection>
        <BottomSection style={{ alignItems: 'baseline' }}>
          <Dialog open={open} handleClose={() => setOpen(false)}>
            <div
              style={{
                fontSize: '2rem',
                width: '100%',
                height: '100%',
                zIndex: 0,
              }}
            >
              {media !== null && (
                <DialogDetailPage
                  option={datas.medias[media].name}
                  contents={[
                    {
                      title: '연령대별 이용률',
                      name: `"${datas.medias[media].name}" 매체는`,
                      values: [...datas.medias[media].values],
                      type: 'columnChart',
                    },
                  ]}
                  goToNextPage={goToNext}
                />
              )}
            </div>
          </Dialog>
          <div style={{ display: 'flex' }}>
            {datas.medias.map((mediaItem) => (
              <ButtonDiv>
                <SquareButton
                  key={mediaItem.id}
                  backImg={mediaItem.imgSrc}
                  onClick={() => {
                    setMedia(mediaItem.id);
                    setOpen(true);
                  }}
                >
                  <div style={{ marginTop: '11rem' }}>{mediaItem.name}</div>
                </SquareButton>

                <TextDiv>{mediaItem.text}</TextDiv>
              </ButtonDiv>
            ))}
          </div>
        </BottomSection>
      </Container>
    </BasePage>
  );
};

export default Page;
