import styled from 'styled-components';
import defaultTheme, { defaultShadow } from 'styles/theme';

const WidthButton = styled.button`
  outline: 0;
  border: 0;
  border-radius: 1rem;
  background-color: ${defaultTheme.palette.mint};
  font-family: 'Spotlight', sans-serif;
  cursor: pointer;
  padding: 0.7rem 5rem;
  font-size: 1.8rem;
  color: #fff;
  margin-top: 3rem;
  box-shadow: ${defaultShadow};
`;

export default WidthButton;
