import styled from 'styled-components';
import defaultTheme from 'styles/theme';

type BtnProps = {
  isActive?: boolean;
};

const CircleButton = styled.button<BtnProps>`
  background-color: ${({ isActive }): string =>
    isActive ? defaultTheme.palette.lightMint : defaultTheme.palette.whiteGray};

  color: ${({ isActive }): string =>
    isActive ? '#fff' : defaultTheme.palette.lightGray};

  outline: 0;
  border: 0;
  border-radius: 50%;
  font-family: 'Spotlight', sans-serif;
  width: 10rem;
  height: 10rem;
  margin: 0 0.5rem 0.5rem 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;

  & img {
    mix-blend-mode: ${({ isActive }): string => (isActive ? '' : 'luminosity')};
  }
`;

export default CircleButton;
