import React from 'react';
import styled, { css } from 'styled-components';
import { darken, lighten } from 'polished';
import { ColorType, Theme } from 'styles/theme';

type SizeType = 'xlarge' | 'large' | 'medium' | 'small';
type FontFamilyType = 'Spoqa' | 'Spotlight' | 'Yj';
type StyledButtonProps = {
  theme: Theme;
  color: ColorType;
  size: SizeType;
  fullWidth: boolean;
  outline: boolean;
  fontFamily: FontFamilyType;
};

const colorStyles = css`
  ${({ theme, color }: StyledButtonProps) => {
    const selected = theme.palette[color];
    return css`
      background: ${selected};
      &:hover {
        background: ${lighten(0.1, selected)};
      }
      &:active {
        background: ${darken(0.1, selected)};
      }
      &:disabled {
        background: ${lighten(0.4, selected)};
        cursor: not-allowed;
      }
      ${(props: StyledButtonProps) =>
        props.outline &&
        css`
          color: ${selected};
          background: none;
          border: 1px solid ${selected};
          &:hover {
            background: ${selected};
            color: white;
          }
          &:active {
            background: ${darken(0.1, selected)};
          }
        `}
    `;
  }}
`;

const sizes = {
  xlarge: {
    height: '3.75rem',
    fontSize: '2rem',
  },
  large: {
    height: '3rem',
    fontSize: '1.25rem',
  },
  medium: {
    height: '2.25rem',
    fontSize: '1rem',
  },
  small: {
    height: '1.75rem',
    fontSize: '0.875rem',
  },
};

const sizeStyles = css`
  ${({ size }: StyledButtonProps) =>
    css`
      height: ${sizes[size].height};
      font-size: ${sizes[size].fontSize};
    `}
`;
const fullWidthStyle = css`
  ${(props: StyledButtonProps) =>
    props.fullWidth &&
    css`
      width: 100%;
      justify-content: center;
      &:not(:first-child) {
        margin-left: 0;
        margin-top: 1rem;
      }
    `}
`;

const fontFamilyStyle = css`
  ${({ fontFamily }: StyledButtonProps) =>
    css`
      font-family: '${fontFamily}', sans-serif;
    `}
`;

const StyledButton = styled.button<StyledButtonProps>`
  /* common style */
  display: inline-flex;
  outline: none;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  padding: 1rem;
  align-items: center;

  /* etc */
  &:not(:first-child) {
    margin-left: 1rem;
  }

  /* size */
  ${sizeStyles}

  /* color */
  ${colorStyles}

  /* width */
  ${fullWidthStyle}

  /* font-family */
  ${fontFamilyStyle}
`;

type ButtonProps = {
  color?: ColorType;
  size?: SizeType;
  outline?: boolean;
  fullWidth?: boolean;
  disabled?: boolean;
  fontFamily?: FontFamilyType;
  onClick?: () => void;
};

const Button: React.FC<ButtonProps> = ({
  children,
  color = 'mint',
  size = 'medium',
  outline = false,
  fullWidth = false,
  fontFamily = 'Spoqa',
  ...rest
}) => {
  return (
    <StyledButton
      color={color}
      size={size}
      outline={outline}
      fullWidth={fullWidth}
      fontFamily={fontFamily}
      {...rest}
    >
      {children}
    </StyledButton>
  );
};

export default Button;
