import React, { useContext, useEffect, useState } from 'react';
import styled from 'styled-components';
import defaultTheme, { defaultShadow } from 'styles/theme';
import SimulatorContext from 'components/Simulator/context';
import PageTitle from 'components/common/PageTitle';
import PageSubtitle from 'components/common/PageSubtitle';
import TopSection from 'components/common/TopSection';
import BottomSection from 'components/common/BottomSection';
import Container from 'components/common/Container';
import CircleButton from 'components/common/CircleButton';
import WidthButton from 'components/common/WidthButton';
import Dialog from 'components/common/Dialog';
import { datas, ages, tags } from 'utils/constants';
import { Radar } from 'react-chartjs-2';
import Chart from 'react-google-charts';
import DialogDetailPage from '../DialogDetailPage';
import BasePage from '../BasePage';

const NameTitle = styled.div`
  font-family: 'Spotlight', sans-serif;
  font-size: 1.9rem;
  color: ${defaultTheme.palette.darkGray};
  margin-bottom: 2rem;
`;
const ContentDiv = styled.div`
  font-size: 1.3rem;
  color: ${defaultTheme.palette.darkGray};
  font-family: 'Spoqa', sans-serif;
  line-height: 2rem;
  height: 9rem;
`;
const ModalButton = styled.button`
  outline: 0;
  border: 0;
  border-radius: 0.5rem;
  background-color: ${defaultTheme.palette.darkBlue};
  color: #fff;
  font-family: 'Spoqa', sans-serif;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0.7rem 2.3rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 800;
  box-shadow: ${defaultShadow};
`;
const GraphsDiv = styled.div`
  display: flex;
  justify-content: space-between;
`;
const GrayDiv = styled.div`
  border-radius: 0.5rem;
  background-color: ${defaultTheme.palette.whiteGray};
  padding: 1rem;
  width: 50%;
  height: 19rem;
  margin-top: 1rem;
  margin-right: 1rem;
`;
const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100%;
`;
const LeftDiv = styled.div`
  margin-left: 3.5rem;
  width: 100%;
  height: 100%;
`;
const ModelImg = styled.img`
  width: 7rem;
  height: 7rem;
`;

const Page: React.FC = () => {
  const {
    state: { product, model },
    setState,
    goToNext,
  } = useContext(SimulatorContext);
  const [open, setOpen] = useState(false);
  const setModel = (modelId: number) =>
    setState((currentState) => {
      return {
        ...currentState,
        model: modelId,
      };
    });
  useEffect(() => {
    if (model === null) {
      setModel(0);
    }
  });

  if (model === null) {
    return <div>...</div>;
  }

  const AgeLikeness = ages.map((i, idx) => [
    ages[idx],
    datas.models[model].values[idx],
  ]);

  const ModelImage = datas.models[model].tags;
  const ModelImageGraph = {
    labels: tags,
    datasets: [
      {
        label: '',
        backgroundColor: 'rgba(179,181,198,0.2)',
        borderColor: 'rgba(179,181,198,1)',
        pointBackgroundColor: 'rgba(179,181,198,1)',
        pointBorderColor: '#fff',
        pointHoverBackgroundColor: '#fff',
        pointHoverBorderColor: 'rgba(179,181,198,1)',
        data: ModelImage,
      },
    ],
  };
  const options = {
    legend: { display: false },
    title: {
      display: true,
      text: '광고감성 적합도',
    },
  };

  return (
    <BasePage>
      <Container>
        <TopSection>
          <PageTitle>광고 모델 고르기</PageTitle>
          <PageSubtitle>
            제품과 광고 매체별 특성과 의사결정을 돕는 설명 글을 이해하고, 제품과
            가장 알맞는 광고 모델을 고르세요!
          </PageSubtitle>
        </TopSection>

        <BottomSection
          style={{ justifyContent: 'space-between', width: '75%' }}
        >
          <Grid>
            {datas.models.map((modelItem) => (
              <CircleButton
                key={modelItem.id}
                isActive={model === modelItem.id}
                onClick={() => {
                  setModel(modelItem.id);
                }}
              >
                <ModelImg src={modelItem.imgSrc} />
              </CircleButton>
            ))}
          </Grid>
          <LeftDiv>
            <NameTitle>
              {datas.models[model].name}({datas.models[model].age}),{' '}
              {datas.models[model].role}
            </NameTitle>
            <ContentDiv>
              <pre>{datas.models[model].desc}</pre>
            </ContentDiv>
            <GraphsDiv>
              <GrayDiv>
                <Chart
                  chartType="LineChart"
                  columns={[
                    {
                      type: 'string',
                      label: '연령대',
                    },
                    {
                      type: 'number',
                      label: '선호도',
                    },
                  ]}
                  rows={AgeLikeness}
                  options={{
                    title: '연령대별 온라인 검색량',
                    chartArea: { width: '100%' },
                    backgroundColor: 'transparent',
                    colors: ['d9d9d9'],
                    fontSize: 12,
                    hAxis: {
                      minValue: 0,
                    },
                    vAxis: {
                      minValue: 0,
                      maxValue: 10,
                    },
                    legend: 'none',
                    animation: {
                      duration: 500,
                    },
                  }}
                />
              </GrayDiv>
              <GrayDiv>
                <Radar data={ModelImageGraph} options={options} />
              </GrayDiv>
            </GraphsDiv>
          </LeftDiv>

          <ModalButton
            onClick={() => {
              if (product !== null) {
                setOpen(true);
              }
            }}
          >
            {product !== null ? datas.products[product].name : ''}
            &nbsp;특성 보기
          </ModalButton>

          <Dialog open={open} handleClose={() => setOpen(false)}>
            <div
              style={{
                fontSize: '2rem',
                width: '100%',
                height: '100%',
                zIndex: 0,
              }}
            >
              {product !== null && (
                <DialogDetailPage
                  option={datas.products[product].name}
                  contents={[
                    {
                      title: '연령대별 선호도',
                      name: `"${datas.products[product].name}" 제품은`,
                      values: [...datas.products[product].values],
                      type: 'columnChart',
                    },
                    {
                      title: '키워드',
                      name: datas.products[product].name,
                      values: [...datas.products[product].keywords],
                      type: 'bubbleChart',
                    },
                  ]}
                />
              )}
            </div>
          </Dialog>
        </BottomSection>
        <WidthButton onClick={goToNext}>다음</WidthButton>
      </Container>
    </BasePage>
  );
};

export default Page;
