import { createContext } from 'react';

export type StateType = {
  page: number;
  product: number | null;
  media: number | null;
  model: number | null;
};

type SimulatorContextProps = {
  state: StateType;
  setState: React.Dispatch<React.SetStateAction<StateType>>;
  goToHome: () => void;
  goToPrev: () => void;
  goToNext: () => void;
};

const SimulatorContext = createContext<SimulatorContextProps>({
  state: {
    page: 0,
    product: null,
    media: null,
    model: null,
  },
  setState: () => {},
  goToHome: () => {},
  goToPrev: () => {},
  goToNext: () => {},
});
export default SimulatorContext;
