import React from 'react';
import styled, { css } from 'styled-components';
import { Theme } from 'styles/theme';
import { Flicker } from 'components/common/animations';

type ResultDetailProps = {
  title: string;
  imgSrc?: string;
};

const ResultContainer = styled.div`
  font-family: 'Spotlight';
  flex: 1;
  text-align: center;
  display: flex;
  flex-direction: column;
  position: relative;

  & + & {
    margin-left: 25px;
  }

  img {
    position: absolute;
    top: 0;
  }

  .effect {
    display: flex;
    justify-content: center;
    animation: ${Flicker} 2s 1 normal forwards;
  }
`;

const Title = styled.div`
  padding: 2rem 0;
  font-size: 2rem;
`;

const Info = styled.div`
  height: 100%;
  border: 1px solid #d9d9d9;
  border-radius: 30px;
  font-size: 1.75rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  ${({ theme }) => css`
    color: ${(theme as Theme).palette.darkGray};
  `}
`;

const ResultDetail: React.FC<ResultDetailProps> = ({
  title,
  imgSrc,
  children,
}) => {
  return (
    <ResultContainer>
      {imgSrc && (
        <div className="effect">
          <img src={imgSrc} alt="" />
        </div>
      )}
      <Title>{title}</Title>
      <Info>{children}</Info>
    </ResultContainer>
  );
};

export default ResultDetail;
