import React, { useContext } from 'react';
import styled from 'styled-components';
import SimulatorContext from 'components/Simulator/context';
import { ReactComponent as Home } from 'assets/images/home.svg';
import { ReactComponent as Left } from 'assets/images/left.svg';
import defaultTheme from 'styles/theme';
import { darken } from 'polished';

const Container = styled.div`
  width: 100vw;
  height: 100vh;
`;
const StyledButton = styled.button`
  position: absolute;
  top: 1.5rem;
  outline: 0;
  border: 0;
  background: 0;
  cursor: pointer;

  &:hover:not(:disabled) {
    path {
      fill: ${darken(0.1, '#d9d9d9')};
    }
  }
  &:active {
    path {
      fill: ${darken(0.1, '#d9d9d9')};
    }
  }
  &:disabled {
    visibility: hidden;
  }
  &:focus {
    outline: 0;
  }
`;
const HomeButton = styled(StyledButton)`
  left: 1.5rem;
`;
const LeftButton = styled(StyledButton)`
  right: 1.5rem;
`;
const Datadriven = styled.div`
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  color: ${defaultTheme.palette.darkGray};
  font-size: 0.8rem;
  font-weight: 800;
  letter-spacing: -0.05rem;
`;

type BasePageProps = {
  hideButtonGroup?: boolean;
};

const BasePage: React.FC<BasePageProps> = ({ children, hideButtonGroup }) => {
  const { goToHome, goToPrev } = useContext(SimulatorContext);
  return (
    <Container>
      {children}
      {!hideButtonGroup && (
        <>
          <HomeButton onClick={goToHome}>
            <Home />
          </HomeButton>
          <LeftButton onClick={goToPrev}>
            <Left />
          </LeftButton>
        </>
      )}
      <Datadriven>© 2021. 01. DataDriven. All rights reserved.</Datadriven>
    </Container>
  );
};

export default BasePage;
