import React from 'react';
import styled, { css } from 'styled-components';
import { Theme, defaultShadow, ColorType } from 'styles/theme';

type StyledButtonProps = {
  theme: Theme;
  color: ColorType;
  backImg: string;
};

const backgroundStyles = css`
  ${({ theme, color, backImg }: StyledButtonProps) =>
    css`
      background-color: ${theme.palette[color]};
      background-image: url(${backImg});
    `}
`;

const StyledButton = styled.button<StyledButtonProps>`
  /* common style */
  width: 17rem;
  height: 17rem;
  border-radius: 2rem;
  color: #fff;
  font-size: 2rem;
  font-family: 'Spotlight', sans-serif;
  margin: 1rem;
  outline: 0;
  border: 0;
  cursor: pointer;
  box-shadow: ${defaultShadow};
  background-size: cover;

  ${backgroundStyles};
`;

type ButtonProps = {
  color?: ColorType;
  backImg?: string;
  onClick?: () => void;
};

const SquareButton: React.FC<ButtonProps> = ({
  children,
  color = 'lightGray',
  backImg = '',
  ...rest
}) => {
  return (
    <StyledButton backImg={backImg} color={color} {...rest}>
      {children}
    </StyledButton>
  );
};

export default SquareButton;
