import React, { useContext, useEffect, useState } from 'react';
import styled, { css } from 'styled-components';
import { Theme } from 'styles/theme';
import SimulatorContext from 'components/Simulator/context';
import Container from 'components/common/Container';
import BottomSection from 'components/common/BottomSection';
import PageTitle from 'components/common/PageTitle';
import TopSection from 'components/common/TopSection';
import ResultDetail from 'components/ResultDetail';
import Button from 'components/common/Button';
import EffectHeart from 'assets/images/effect_heart.svg';
import EffectStar from 'assets/images/effect_star.svg';
import { datas, keywordMap } from 'utils/constants';
import { getResult } from 'utils/result';
import BasePage from '../BasePage';

const StepList = styled.ul`
  font-family: 'Spoqa';
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3.5rem;
`;

type StepProps = {
  active: boolean;
};
const Step = styled.li<StepProps>`
  font-size: 1.75rem;
  font-weight: bold;
  text-align: center;
  padding: 1rem 2rem;
  border-radius: 30px;
  line-height: 0.87;
  letter-spacing: -0.6px;

  & + & {
    margin-left: 40px;
  }

  ${({ theme }) => css`
    color: ${(theme as Theme).palette.lightGray};
    background: ${(theme as Theme).palette.whiteGray};
  `}

  ${(props) =>
    props.active &&
    css`
      color: white;
      background-color: ${(props.theme as Theme).palette.lightMint};
    `}
`;

const BottomContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const ResultContainer = styled.div`
  display: flex;
  height: 55vh;
  margin: 2rem 9rem;
`;

const ResultDetailBigValue = styled.div`
  font-size: 7rem;
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  ${({ theme }) => css`
    color: ${(theme as Theme).palette.darkBlue};
  `}

  span {
    padding: 0 5.5rem;
  }

  sub {
    font-size: 3rem;
    width: 0;
    display: inline-block;
  }
`;

const ResultDetailFooter = styled.ul`
  padding: 2rem 0;
  display: flex;
  justify-content: center;

  ${({ theme }) => css`
    border-top: 1px solid ${(theme as Theme).palette.lightGray};
  `}

  li + li {
    margin-left: 2.5rem;
  }
`;

const ResultDetailDescription = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  line-height: 2.5rem;

  br {
    display: block;
    margin: 10px 0;
  }
`;

const ButtonGroup = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
`;

const Page: React.FC = () => {
  const { state, goToNext } = useContext(SimulatorContext);
  const [step, setStep] = useState(0);
  const [canGoNext, setCanGoNext] = useState(false);
  const stepLength = 4;
  const goToNextStep = () =>
    setStep((currentStep) => (currentStep + 1) % stepLength);
  useEffect(() => {
    if (step < stepLength - 1) {
      setTimeout(() => {
        goToNextStep();
      }, 3000);
    } else {
      setCanGoNext(true);
    }
  }, [step]);

  if (state.product === null || state.media === null || state.model === null) {
    return <div>선택하지 않은 옵션이 있습니다.</div>;
  }
  const result = getResult(state.product, state.media, state.model);
  const steps = [
    {
      id: 0,
      name: '제품 선택 결과',
      result: (
        <ResultContainer>
          <ResultDetail title="제품 선호도" imgSrc={EffectHeart}>
            <ResultDetailBigValue>
              <div>
                <span>{result.productResult.peoplePercent}</span>
                <sub>%</sub>
              </div>
            </ResultDetailBigValue>
            <ResultDetailFooter>
              <li>결과값</li>
              <li>
                {result.productResult.beforePeople}명 →️{' '}
                {result.productResult.afterPeople}명
              </li>
            </ResultDetailFooter>
          </ResultDetail>
          <ResultDetail title="선호 연령대" imgSrc={EffectStar}>
            <ResultDetailBigValue>
              <div>
                <span>{result.productResult.topAge.split('대')[0]}</span>
                <sub>대</sub>
              </div>
            </ResultDetailBigValue>
            <ResultDetailFooter>
              <li>점유율</li>
              <li>{result.productResult.topAgePercent}%</li>
            </ResultDetailFooter>
          </ResultDetail>
          <ResultDetail title="상세 설명">
            <ResultDetailDescription>
              전체 인원 {result.productResult.beforePeople}명 중<br />{' '}
              {datas.products[state.product].name} 제품을 <br /> 좋아하는 사람은
              {result.productResult.afterPeople}명으로 <br />총{' '}
              {result.productResult.peoplePercent}%의 선호도를 보입니다. <br />
              <br /> 가장 좋아하는 연령대는 <br /> {result.productResult.topAge}
              로 {result.productResult.topAgePercent}% 의 비율을
              <br />
              나타냅니다.
            </ResultDetailDescription>
          </ResultDetail>
        </ResultContainer>
      ),
    },
    {
      id: 1,
      name: '매체 선택 결과',
      result: (
        <ResultContainer>
          <ResultDetail title="매체 선호도">
            <ResultDetailBigValue>
              <div>
                <span>{result.mediaResult.peoplePercent}</span>
                <sub>%</sub>
              </div>
            </ResultDetailBigValue>
            <ResultDetailFooter>
              <li>결과값</li>
              <li>
                {result.mediaResult.beforePeople}명 →{' '}
                {result.mediaResult.afterPeople}명
              </li>
            </ResultDetailFooter>
          </ResultDetail>
          <ResultDetail title="선호 연령대">
            <ResultDetailBigValue>
              <div>
                <span>{result.mediaResult.topAge.split('대')[0]}</span>
                <sub>대</sub>
              </div>
            </ResultDetailBigValue>
            <ResultDetailFooter>
              <li>점유율</li>
              <li>{result.mediaResult.topAgePercent}%</li>
            </ResultDetailFooter>
          </ResultDetail>
          <ResultDetail title="상세 설명">
            <ResultDetailDescription>
              전체 인원 {result.mediaResult.beforePeople}명 중<br />{' '}
              {datas.medias[state.media].name} 매체를 <br /> 좋아하는 사람은
              {result.mediaResult.afterPeople}명으로 <br />총{' '}
              {result.mediaResult.peoplePercent}%의 선호도를 보입니다. <br />
              <br /> 가장 좋아하는 연령대는 <br /> {
                result.mediaResult.topAge
              }로 {result.mediaResult.topAgePercent}% 의 비율을
              <br />
              나타냅니다.
            </ResultDetailDescription>
          </ResultDetail>
        </ResultContainer>
      ),
    },
    {
      id: 2,
      name: '모델 선호도 결과',
      result: (
        <ResultContainer>
          <ResultDetail title="모델 선호도">
            <ResultDetailBigValue>
              <div>
                <span>{result.modelResult.peoplePercent}</span>
                <sub>%</sub>
              </div>
            </ResultDetailBigValue>
            <ResultDetailFooter>
              <li>결과값</li>
              <li>
                {result.modelResult.beforePeople}명 →{' '}
                {result.modelResult.afterPeople}명
              </li>
            </ResultDetailFooter>
          </ResultDetail>
          <ResultDetail title="선호 연령대">
            <ResultDetailBigValue>
              <div>
                <span>{result.modelResult.topAge.split('대')[0]}</span>
                <sub>대</sub>
              </div>
            </ResultDetailBigValue>
            <ResultDetailFooter>
              <li>점유율</li>
              <li>{result.modelResult.topAgePercent}%</li>
            </ResultDetailFooter>
          </ResultDetail>
          <ResultDetail title="상세 설명">
            <ResultDetailDescription>
              전체 인원 {result.modelResult.beforePeople}명 중<br />{' '}
              {datas.models[state.model].name} 모델을 <br /> 좋아하는 사람은
              {result.modelResult.afterPeople}명으로 <br />총{' '}
              {result.modelResult.peoplePercent}%의 선호도를 보입니다. <br />
              <br /> 가장 좋아하는 연령대는 <br /> {
                result.modelResult.topAge
              }로 {result.modelResult.topAgePercent}% 의 비율을
              <br />
              나타냅니다.
            </ResultDetailDescription>
          </ResultDetail>
        </ResultContainer>
      ),
    },
    {
      id: 3,
      name: '구매의사',
      result: (
        <ResultContainer>
          <ResultDetail title="광고 도달률">
            <ResultDetailBigValue>
              <div>
                <span>{result.finalResult.peoplePercent}</span>
                <sub>%</sub>
              </div>
            </ResultDetailBigValue>
            <ResultDetailFooter>
              <li>결과값</li>
              <li>
                {result.productResult.beforePeople}명 →️{' '}
                {result.finalResult.afterPeople}명
              </li>
            </ResultDetailFooter>
          </ResultDetail>
          <ResultDetail title="키워드 유사도">
            <ResultDetailBigValue>
              <div>
                <span>{result.finalResult.score}</span>
                <sub>%</sub>
              </div>
            </ResultDetailBigValue>
            <ResultDetailFooter>
              {datas.products[state.product].keywords
                .map((keyword) => keywordMap.get(keyword))
                .filter((v, idx, arr) => arr.indexOf(v) === idx)
                .map((tag) => (
                  <li>{tag}</li>
                ))}
            </ResultDetailFooter>
          </ResultDetail>
          <ResultDetail title="상세 설명">
            <ResultDetailDescription>
              제품, 매체, 모델 선택의 결과
              <br />
              광고를 시청한 사람은
              <br />
              {result.productResult.beforePeople}명 중{' '}
              {result.finalResult.afterPeople}명으로
              <br />총 {result.finalResult.peoplePercent}%가 시청하였습니다.
              <br />
              <br />
              제품과 모델의 키워드를 분석했을 때
              <br />
              매칭된 관심 키워드는
              <br />
              {datas.products[state.product].keywords
                .map((keyword) => keywordMap.get(keyword))
                .filter((v, idx, arr) => arr.indexOf(v) === idx)
                .join(', ')}{' '}
              입니다.
            </ResultDetailDescription>
          </ResultDetail>
        </ResultContainer>
      ),
    },
  ];

  return (
    <BasePage>
      <Container>
        <TopSection>
          <PageTitle>시뮬레이션 결과 분석</PageTitle>
          <StepList>
            {steps.map((item) => (
              <Step key={item.id} active={item.id <= step}>
                {item.name}
              </Step>
            ))}
          </StepList>
        </TopSection>
        <BottomSection>
          <BottomContainer>
            {steps[step].result}
            <ButtonGroup>
              <Button size="xlarge" disabled={!canGoNext} onClick={goToNext}>
                다음
              </Button>
            </ButtonGroup>
          </BottomContainer>
        </BottomSection>
      </Container>
    </BasePage>
  );
};

export default Page;
