import React from 'react';
import styled from 'styled-components';
import * as Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import { ages } from 'utils/constants';

export type ContentType = {
  title: string;
  name: string;
  values: (string | number)[];
  type: string;
};

type ContentDetailProps = {
  content: ContentType;
};

const DescDiv = styled.div`
  font-size: 1.7rem;
  color: #717578;
  line-height: 2.75rem;
  padding: 0 1rem;
`;

Highcharts.setOptions({
  chart: {
    style: {
      fontFamily: 'Spotlight, sans-serif',
    },
  },
});

const ContentDetail: React.FC<ContentDetailProps> = ({ content }) => {
  switch (content.type) {
    case 'columnChart': {
      const maxIdx = (content.values as number[]).reduce(
        (acc, current, idx, arr) => (arr[acc] > arr[idx] ? acc : idx),
        0,
      );
      const colors = content.values.map((val, idx) =>
        idx === maxIdx ? '#1B94A3' : '#D9D9D9',
      );

      return (
        <div style={{ display: 'flex', width: '100%' }}>
          <div
            style={{
              width: '50%',
              height: '100%',
              flex: 1,
            }}
          >
            <HighchartsReact
              highcharts={Highcharts}
              options={{
                credits: { enabled: false },
                chart: {
                  type: 'column',
                  backgroundColor: 'rgba(255, 255, 255, 0)',
                },
                title: {
                  text: '연령대별 선호도',
                  margin: 30,
                  style: { fontSize: '2rem' },
                },
                plotOptions: {
                  series: {
                    colorByPoint: true,
                    enableMouseTracking: false,
                    dataLabels: {
                      enabled: true,
                      style: { fontSize: '1.125rem' },
                    },
                  },
                },
                colors,
                xAxis: {
                  categories: ages,
                  labels: { style: { fontSize: '1.25rem' } },
                },
                yAxis: {
                  title: '',
                  min: 0,
                  max: 100,
                  tickInterval: 50,
                  labels: { style: { fontSize: '1.25rem' } },
                },
                series: [
                  {
                    name: '선호도',
                    data: content.values,
                  },
                ],
                legend: {
                  enabled: false,
                },
                tooltip: {
                  enabled: false,
                },
              }}
            />
          </div>
          <div
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              width: '100%',
              flex: 1,
            }}
          >
            <DescDiv>
              각 연령대별 100명씩 총 600명에게 실시한 선호도 조사 결과,
              <br />
              {content.name}
              <br />
              <ul>
                {content.values.map((value, idx, arr) => (
                  <>
                    <span>
                      {ages[idx]} {value}명
                      {idx === arr.length - 1 ? '으로 나타났어.' : ', '}
                    </span>
                    {(idx + 1) % 3 === 0 && <br />}
                  </>
                ))}
              </ul>
            </DescDiv>
          </div>
        </div>
      );
    }
    case 'bubbleChart': {
      const colors = ['#77D6E7', '#2594A2', '#F1D682', '#6BD5C4', '#F1AD92'];
      const bubbles = content.values.map((value, idx, arr) => {
        const degrees = 90 + idx * Math.floor(360 / arr.length);
        const size = 10 + Math.floor((value as string).length / 5);
        const rate = 2.5;
        const x = Math.cos((degrees * Math.PI) / 180) / rate;
        const xPos = x * 50 + 50;
        const y = Math.sin((degrees * Math.PI) / 180) / rate;
        const yPos = 50 - y * 50;
        return {
          size: `${size}rem`,
          top: `calc(${xPos}% - ${size / 2}rem)`,
          left: `calc(${yPos}% - ${size / 2}rem)`,
          background: colors[idx % colors.length],
        };
      });
      return (
        <div
          style={{
            width: '45%',
            height: '60%',
            position: 'relative',
          }}
        >
          {content.values.map((value, idx) => {
            const bubble = bubbles[idx];
            return (
              <div
                key={value}
                style={{
                  width: bubble.size,
                  height: bubble.size,
                  borderRadius: '50%',
                  background: bubble.background,
                  opacity: 0.9,
                  color: 'white',
                  position: 'absolute',
                  top: bubble.top,
                  left: bubble.left,
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                <span>{value}</span>
              </div>
            );
          })}
        </div>
      );
    }
    default:
      return <div>콘텐츠 정보를 찾을 수 없음</div>;
  }
};

export default ContentDetail;
