import { ages, datas, getTagValues } from './constants';

export const getCosSimilarityScore: (
  vec1: number[],
  vec2: number[],
) => number = (vec1, vec2) => {
  if (vec1.length !== vec2.length) {
    return 0;
  }
  const sum = (values: number[]) =>
    values.reduce((acc, current) => acc + current, 0);
  const cosSimiliraty =
    sum(vec1.map((value, idx) => value * vec2[idx])) /
    (Math.sqrt(sum(vec1.map((value) => value ** 2))) *
      Math.sqrt(sum(vec2.map((value) => value ** 2))));
  return Math.max(Math.floor(cosSimiliraty * 100), 0);
};

export const getResult: (
  product: number,
  media: number,
  model: number,
) => {
  productResult: {
    peoplePercent: number;
    beforePeople: number;
    afterPeople: number;
    topAge: string;
    topAgePercent: number;
    stepValues: number[];
  };
  mediaResult: {
    peoplePercent: number;
    beforePeople: number;
    afterPeople: number;
    topAge: string;
    topAgePercent: number;
    stepValues: number[];
  };
  modelResult: {
    peoplePercent: number;
    beforePeople: number;
    afterPeople: number;
    topAge: string;
    topAgePercent: number;
    stepValues: number[];
  };
  finalResult: {
    peoplePercent: number;
    beforePeople: number;
    afterPeople: number;
    stepValues: number[];
    score: number;
  };
} = (product, media, model) => {
  const people = 100;
  const totalPeople = people * ages.length;

  const sum = (values: number[]) =>
    values.reduce((acc, current) => acc + current, 0);
  const maxIdx = (values: number[]) =>
    values.reduce(
      (acc, current, idx, arr) => (arr[acc] > arr[idx] ? acc : idx),
      0,
    );

  const productValues = [...datas.products[product].values];
  const step0Values = productValues;
  const productTopIdx = maxIdx(productValues);
  const step0RemainPeople = sum(step0Values);
  const productResult = {
    peoplePercent: Math.floor((step0RemainPeople * 100) / totalPeople),
    beforePeople: totalPeople,
    afterPeople: step0RemainPeople,
    topAge: ages[productTopIdx],
    topAgePercent: Math.floor(
      (productValues[productTopIdx] * 100) / sum(productValues),
    ),
    stepValues: step0Values,
  };

  const mediaValues = [...datas.medias[media].values];
  const step1Values = mediaValues.map((value, idx) =>
    Math.floor((value * step0Values[idx]) / 100),
  );
  const mediaTopIdx = maxIdx(mediaValues);
  const step1RemainPeople = sum(step1Values);
  const mediaResult = {
    peoplePercent: Math.floor((step1RemainPeople * 100) / step0RemainPeople),
    beforePeople: step0RemainPeople,
    afterPeople: step1RemainPeople,
    topAge: ages[mediaTopIdx],
    topAgePercent: Math.floor(
      (mediaValues[mediaTopIdx] * 100) / sum(mediaValues),
    ),
    stepValues: step1Values,
  };

  const modelValues = [...datas.models[model].values];
  const step2Values = modelValues.map((value, idx) =>
    Math.floor((value * step1Values[idx]) / 100),
  );
  const modelTopIdx = maxIdx(modelValues);
  const step2RemainPeople = sum(step2Values);
  const modelResult = {
    peoplePercent: Math.floor((step2RemainPeople * 100) / step1RemainPeople),
    beforePeople: step1RemainPeople,
    afterPeople: step2RemainPeople,
    topAge: ages[modelTopIdx],
    topAgePercent: Math.floor(
      (modelValues[modelTopIdx] * 100) / sum(modelValues),
    ),
    stepValues: step2Values,
  };

  const tagValues = getTagValues(datas.products[product].keywords);
  const tagMatchingScore = getCosSimilarityScore(
    tagValues,
    datas.models[model].tags,
  );
  const step3Values = step2Values.map((value) =>
    Math.floor((value * tagMatchingScore) / 100),
  );
  const step3RemainPeople = sum(step3Values);
  const finalResult = {
    peoplePercent: Math.floor((step3RemainPeople * 100) / totalPeople),
    beforePeople: step2RemainPeople,
    afterPeople: step3RemainPeople,
    stepValues: step3Values,
    score: tagMatchingScore,
  };

  return {
    productResult,
    mediaResult,
    modelResult,
    finalResult,
  };
};

type RankInfoType = {
  key: string;
  description: string;
  value: number;
};

export const getRankList: (product: number) => RankInfoType[] = (product) => {
  const productName = datas.products[product].name;
  const rankList: RankInfoType[] = [];

  datas.medias.forEach((media) => {
    datas.models.forEach((model) => {
      const key = [product, media.id, model.id].join('&');
      const description = [productName, media.name, model.name].join(', ');
      const result = getResult(product, media.id, model.id);
      rankList.push({
        key,
        description,
        value: result.finalResult.afterPeople,
      });
    });
  });
  return rankList.sort((a, b) => b.value - a.value);
};
