import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { darken } from 'polished';
import Button from 'components/common/Button';
import ContentDetail, { ContentType } from 'components/ContentDetail';
import { ReactComponent as Left } from 'assets/images/left.svg';

const StyledButton = styled.button`
  border: 0;
  background: transparent;
  font-size: 2rem;
  z-index: 2;
  cursor: pointer;
  &:hover:not(:disabled) {
    path {
      fill: ${darken(0.1, '#d9d9d9')};
    }
  }
  &:active {
    path {
      fill: ${darken(0.1, '#d9d9d9')};
    }
  }
  &:disabled {
    visibility: hidden;
  }
  &:focus {
    outline: 0;
  }

  &:last-child {
    svg {
      transform: rotate(180deg);
    }
  }
`;

const ButtonGroup = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: 2rem;
  z-index: -1;
`;

const ContentContainer = styled.div`
  display: flex;
  width: 80%;
  height: 100%;
  margin: auto;
  justify-content: space-between;
  align-items: center;
  font-family: 'Spotlight', sans-serif;
`;

type DialogDetailPageProps = {
  option: string;
  contents: ContentType[];
  goToNextPage?: () => void;
};

const DialogDetailPage: React.FC<DialogDetailPageProps> = ({
  option,
  contents,
  goToNextPage,
}) => {
  const [subPage, setSubPage] = useState(0);
  const [canGoNextPage, setCanGoNextPage] = useState(false);
  const goToPrevSubPage = () => {
    setSubPage((page) => page - 1);
  };
  const goToNextSubPage = () => {
    setSubPage((page) => page + 1);
    setCanGoNextPage(true);
  };
  useEffect(() => {
    setSubPage(0);
    setCanGoNextPage(contents.length === 1);
  }, [option, contents]);
  const content = contents[subPage];
  return (
    <>
      <ContentContainer>
        <ContentDetail content={content} />
      </ContentContainer>
      <ButtonGroup>
        <StyledButton disabled={subPage === 0} onClick={goToPrevSubPage}>
          <Left />
        </StyledButton>
        <StyledButton
          disabled={subPage === contents.length - 1}
          onClick={goToNextSubPage}
        >
          <Left />
        </StyledButton>
      </ButtonGroup>
      {goToNextPage && (
        <ButtonGroup
          style={{
            justifyContent: 'center',
            height: '8rem',
            top: '80%',
            zIndex: 2,
          }}
        >
          <Button
            size="xlarge"
            onClick={goToNextPage}
            disabled={!canGoNextPage}
          >
            {option} 선택하기
          </Button>
        </ButtonGroup>
      )}
    </>
  );
};

export default DialogDetailPage;
