import React, { useContext } from 'react';
import styled from 'styled-components';
import SimulatorContext from 'components/Simulator/context';
import Container from 'components/common/Container';
import PageTitle from 'components/common/PageTitle';
import TopSection from 'components/common/TopSection';
import BottomSection from 'components/common/BottomSection';
import WidthButton from 'components/common/WidthButton';
import Gold from 'assets/images/gold.svg';
import Silver from 'assets/images/silver.svg';
import Bronze from 'assets/images/bronze.svg';
import Shadow from 'assets/images/medal_shadow.png';
import { Bounce } from 'components/common/animations';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import { datas } from 'utils/constants';
import { getResult, getRankList } from 'utils/result';
import BasePage from '../BasePage';

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 1,
    slidesToSlide: 1, // optional, default to 1.
  },
};
const MedalImg = styled.img`
  width: 30rem;
  animation: ${Bounce} 0.7s infinite alternate;
`;
const ShadowImg = styled.img`
  display: flex;
  margin: 0 auto;
`;
const Panel = styled.div`
  width: 50rem;
  height: 16rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
const Title = styled.div`
  font-size: 2.1rem;
  font-family: 'Spotlight', sans-serif;
  letter-spacing: -0.1rem;
  margin-bottom: 1.5rem;
`;
const ContentText = styled.div`
  font-size: 1.4rem;
  line-height: 2rem;
  font-family: 'Spoqa', sans-serif;
  letter-spacing: -0.01rem;
`;
const CustomBottomSection = styled(BottomSection)`
  display: block;
  text-align: center;
  margin-top: 0;
  color: #717578;
`;
const PointText = styled.span`
  color: #1b94a3;
  font-weight: 800;
`;
const Table = styled.table`
  width: 100%;
  font-size: 1.3rem;
  border: 2px solid #d9d9d9;
`;
const Th = styled.th`
  padding: 0.8rem;
  border-bottom: 2px solid #d9d9d9;
  border-right: 2px solid #d9d9d9;
`;
const Tr = styled.tr`
  padding: 0.8rem;
  border-bottom: 2px solid #d9d9d9;
`;
const Td = styled.td`
  padding: 0.8rem;
  border-right: 2px solid #d9d9d9;
  &:first-child {
    width: 18%;
  }
`;

const Page: React.FC = () => {
  const {
    state: { product, media, model },
    goToNext,
  } = useContext(SimulatorContext);

  if (product === null || media === null || model === null) {
    return <div>...</div>;
  }

  const result = getResult(product, media, model);
  const key = [product, media, model].join('&');
  const rankList = getRankList(product);
  const rankRangeSize = Math.floor(rankList.length / 3);
  const rewards = [
    {
      id: 0,
      name: '금상',
      imgSrc: Gold,
      minRank: 0,
      maxRank: rankRangeSize - 1,
    },
    {
      id: 1,
      name: '은상',
      imgSrc: Silver,
      minRank: rankRangeSize,
      maxRank: 2 * rankRangeSize - 1,
    },
    {
      id: 2,
      name: '동상',
      imgSrc: Bronze,
      minRank: 2 * rankRangeSize,
      maxRank: rankList.length - 1,
    },
  ];
  const getRandom = (min: number, max: number) => {
    const minVal = Math.ceil(min);
    const maxVal = Math.floor(max);
    return Math.floor(Math.random() * (maxVal - minVal + 1)) + minVal;
  };

  const myRank = rankList.findIndex((rankInfo) => rankInfo.key === key);
  const myReward = rewards.find(
    (reward) => reward.minRank <= myRank && myRank <= reward.maxRank,
  );

  if (!myReward) {
    return <div>...</div>;
  }

  return (
    <BasePage>
      <Container>
        <TopSection style={{ height: '7rem' }}>
          <PageTitle>광고왕 콘테스트 결과</PageTitle>
        </TopSection>

        <CustomBottomSection>
          <div style={{ display: 'block' }}>
            <MedalImg src={myReward.imgSrc} alt="" />
            <ShadowImg src={Shadow} alt="" />
          </div>
          <div style={{ width: '100%' }}>
            <Carousel
              swipeable
              draggable
              showDots
              responsive={responsive}
              autoPlay={false}
              removeArrowOnDeviceType={['desktop']}
              dotListClass="custom-dot-list-style"
              renderDotsOutside
              itemClass="carousel-item-padding-40-px"
              containerClass="carousel-container"
            >
              <Panel>
                <Title>{myReward.name} 수상을 축하합니다.</Title>
                <ContentText>
                  당신은&nbsp;
                  <PointText>{datas.products[product].name}</PointText>
                  &nbsp;제품을
                  <br />
                  <PointText>{datas.medias[media].name}</PointText>
                  &nbsp;매체와 <PointText>{datas.models[model].name}</PointText>
                  &nbsp;모델을 활용한 기발한 광고를 통해
                  <br />총 {result.finalResult.afterPeople}명에게 구매 유도를
                  성공했습니다.
                </ContentText>
              </Panel>
              <Panel>
                <Table>
                  <Th> </Th>
                  <Th>제품</Th>
                  <Th>매체</Th>
                  <Th>모델</Th>
                  <Th>구매 유도 성공</Th>

                  {rewards.map((reward) => {
                    const rank =
                      reward.id === myReward.id
                        ? myRank
                        : getRandom(reward.minRank, reward.maxRank);
                    const rankInfo = rankList[rank];
                    const [
                      productName,
                      mediaName,
                      modelName,
                    ] = rankInfo.description.split(', ');
                    return (
                      <Tr key={rankInfo.key}>
                        <Td>{reward.name}</Td>
                        <Td>{productName}</Td>
                        <Td>{mediaName}</Td>
                        <Td>{modelName}</Td>
                        <Td>{rankInfo.value}</Td>
                      </Tr>
                    );
                  })}
                </Table>
              </Panel>
            </Carousel>
          </div>
        </CustomBottomSection>

        <WidthButton onClick={goToNext}>시뮬레이션 다시하기</WidthButton>
      </Container>
    </BasePage>
  );
};

export default Page;
