import React from 'react';
import styled, { css } from 'styled-components';
import { ReactComponent as Close } from 'assets/images/close.svg';
import { darken } from 'polished';

type OverlayProps = {
  open: boolean;
};

const Overlay = styled.div<OverlayProps>`
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(248, 248, 248, 0.95);
  z-index: 1;

  ${(props) =>
    !props.open &&
    css`
      display: none;
    `};
`;

const CloseBtn = styled.button`
  border: 0;
  background: transparent;
  position: absolute;
  top: 2rem;
  right: 2rem;
  z-index: 1;
  cursor: pointer;

  &:hover:not(:disabled) {
    path {
      fill: ${darken(0.1, '#d9d9d9')};
    }
  }
  &:active {
    path {
      fill: ${darken(0.1, '#d9d9d9')};
    }
  }
  &:focus {
    outline: 0;
  }
`;

type DialogProps = {
  open: boolean;
  handleClose: () => void;
};

const Dialog: React.FC<DialogProps> = ({ children, open, handleClose }) => {
  return (
    <Overlay open={open}>
      <CloseBtn onClick={handleClose}>
        <Close />
      </CloseBtn>
      {children}
    </Overlay>
  );
};

export default Dialog;
