import React, { useContext, useEffect } from 'react';
import styled from 'styled-components';
import WidthButton from 'components/common/WidthButton';
import Container from 'components/common/Container';
import SimulatorContext from 'components/Simulator/context';
import { Bounce } from 'components/common/animations';
import defaultTheme from 'styles/theme';
import EasyLearning from 'assets/images/easyLearning.png';

const Title = styled.div`
  font-size: 5rem;
  color: ${defaultTheme.palette.mint};
  font-family: 'Yj', sans-serif;
  animation: ${Bounce} 0.7s infinite alternate;
  text-align: center;
  line-height: 6.5rem;
  position: relative;
`;
const Img = styled.img`
  position: absolute;
  top: -8rem;
  left: 0;
`;

const Page: React.FC = () => {
  const { goToNext, state, setState } = useContext(SimulatorContext);
  useEffect(() => {
    if (state.product !== null || state.media !== null || state.model) {
      setState((currentState) => ({
        ...currentState,
        product: null,
        media: null,
        model: null,
      }));
    }
  });
  return (
    <Container>
      <Title>
        <Img src={EasyLearning} alt="" />
        데이터 기반
        <br /> 의사결정 시뮬레이션
      </Title>
      <WidthButton onClick={goToNext}>광고기획사 편</WidthButton>
    </Container>
  );
};

export default Page;
